import { types } from 'src/types/types'

const initialState = []

export const providerReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.getProviders:
      return action.payload
    case types.createProvider:
      return [...state, action.payload]
    case types.updateProvider:
      return state.map((provider) => {
        if (provider.id === action.payload.id) {
          return {
            ...provider,
            ...action.payload,
          }
        } else {
          return provider
        }
      })
    default:
      return state
  }
}
