import React from 'react'

const Dashboard = React.lazy(() => import('./views/dashboard/Dashboard'))

const Customers = React.lazy(() => import('./views/catalogs/customers/Customers'))
const Calendar = React.lazy(() => import('./views/catalogs/calendar/Calendar'))
const Branches = React.lazy(() => import('./views/catalogs/branches/Branches'))
const Providers = React.lazy(() => import('./views/catalogs/providers/Providers'))
const ProductsCategories = React.lazy(() =>
  import('./views/catalogs/products-categories/ProductsCategories'),
)
const Products = React.lazy(() => import('./views/catalogs/products/Products'))
const Sales = React.lazy(() => import('./views/catalogs/sales/Sales'))
const Users = React.lazy(() => import('./views/catalogs/users/Users'))
const Roles = React.lazy(() => import('./views/catalogs/roles/Roles'))

const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/dashboard', name: 'Dashboard', element: Dashboard },
  { path: '/catalogs', name: 'Catálogos', exact: true },
  { path: '/catalogs/customers', name: 'Pacientes', element: Customers },
  { path: '/catalogs/calendar', name: 'Calendario', element: Calendar },
  { path: '/catalogs/branches', name: 'Sucursales', element: Branches },
  { path: '/catalogs/providers', name: 'Proveedores', element: Providers },
  {
    path: '/catalogs/products-categories',
    name: 'Categorias de productos',
    element: ProductsCategories,
  },
  { path: '/catalogs/products', name: 'Productos', element: Products },
  { path: '/catalogs/sales', name: 'Ventas', element: Sales },
  { path: '/catalogs/users', name: 'Usuarios', element: Users },
  { path: '/catalogs/roles', name: 'Roles', element: Roles },
]

export default routes
