import { types } from 'src/types/types'

const initialState = {
  sidebarShow: true,
  loggedIn: false,
  error: false,
}

export const appNavReducer = (state = initialState, { type, ...rest }) => {
  switch (type) {
    case types.set:
      return { ...state, ...rest }
    case types.errorCalendarForm:
      return { ...state, ...rest }
    default:
      return state
  }
}
