import { applyMiddleware, combineReducers, compose, createStore } from 'redux'
import thunk from 'redux-thunk'
import { appNavReducer } from './reducers/appNavReducer'
import { appoimentReducer } from './reducers/appoimentReducer'
import { authReducer } from './reducers/authReducer'
import { branchReducer } from './reducers/branchReducer'
import { customerReducer } from './reducers/customerReducer'
import { productCategoryReducer } from './reducers/productCategoryReducer'
import { productReducer } from './reducers/productReducer'
import { providerReducer } from './reducers/providerReducer'
import { roleReducer } from './reducers/roleReducer'
import { saleReducer } from './reducers/saleReducer'
import { userReducer } from './reducers/userReducer'

const composeEnhancers =
  (typeof window !== 'undefined' && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__) || compose

const reducers = combineReducers({
  navBar: appNavReducer,
  auth: authReducer,
  appoiments: appoimentReducer,
  customers: customerReducer,
  branches: branchReducer,
  products: productReducer,
  providers: providerReducer,
  productCategories: productCategoryReducer,
  sales: saleReducer,
  users: userReducer,
  roles: roleReducer,
})

const store = createStore(reducers, composeEnhancers(applyMiddleware(thunk)))

// const store = createStore(appNavReducer)

export default store
