import jwtDecode from 'jwt-decode'
import { types } from 'src/types/types'

const initialState = {
  loggedIn: false,
  token: localStorage.getItem('jwt'),
  id: null,
  profile: {
    role: {
      permissions: [],
    },
  },
}

export const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.getProfile:
      return { ...state, profile: action.payload, loggedIn: true, id: action.payload.id }
    case types.login:
      const { id } = jwtDecode(action.payload)
      return { ...state, loggedIn: true, token: action.payload, id: id }
    case types.logout:
      return {
        loggedIn: false,
        token: null,
        id: null,
        profile: {
          role: {
            permissions: [],
          },
        },
      }
    default:
      return state
  }
}
