import { types } from 'src/types/types'

const initialState = []

export const branchReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.getBranches:
      return action.payload
    case types.createBranch:
      return [...state, action.payload]
    case types.updateBranch:
      return state.map((branch) => {
        if (branch.id === action.payload.id) {
          return {
            ...branch,
            ...action.payload,
          }
        } else {
          return branch
        }
      })
    default:
      return state
  }
}
