import { types } from 'src/types/types'

const initialState = []

export const appoimentReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.getAppoiments:
      return action.payload
    case types.createAppoiment:
      return [...state, action.payload]
    case types.updateAppoiment:
      return state
    default:
      return state
  }
}
