import axios from 'axios'
import { types } from '../types/types'

export const loginEmailPassword = (email, password) => {
  return (dispatch) => {
    axios
      .post(`${process.env.REACT_APP_API_URL_BASE}/api/auth/signin`, { email, password })
      .then((response) => {
        localStorage.setItem('jwt', response.data.jwt)
        dispatch(login(response.data.jwt))
        dispatch(getUserProfile())
      })
      .catch((error) => {
        console.log(error)
      })
  }
}

export const getUserProfile = () => {
  return async (dispatch, getState) => {
    const token = getState().auth.token

    if (token) {
      await axios
        .get(`${process.env.REACT_APP_API_URL_BASE}/api/auth/profile`, {
          headers: {
            'auth-token': token,
          },
        })
        .then((response) => {
          console.log(response.data)
          dispatch(loadUser(response.data))
        })
        .catch((error) => {
          // console.log(error)
          if (error.response.data.msg === 'Token Expired') {
            localStorage.removeItem('jwt')
            dispatch(logout())
          }
        })
    } else {
      return null
    }
  }
}

export const logoutUser = () => {
  return (dispatch) => {
    localStorage.removeItem('jwt')
    dispatch(logout())
  }
}
export const login = (token) => ({
  type: types.login,
  payload: token,
})

export const logout = () => ({
  type: types.logout,
  payload: null,
})

export const loadUser = (profile) => ({
  type: types.getProfile,
  payload: profile,
})

