import { types } from 'src/types/types'

const initialState = []

export const saleReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.getSales:
      return action.payload
    case types.createSale:
      return [...state, action.payload]
    case types.updateSale:
      return state.map((sale) => {
        if (sale.id === action.payload.id) {
          return {
            ...sale,
            ...action.payload,
          }
        } else {
          return sale
        }
      })
    default:
      return state
  }
}
