import { types } from 'src/types/types'

const initialState = []

export const productCategoryReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.getProductCategories:
      return action.payload
    case types.createProductCategory:
      return [...state, action.payload]
    case types.updateProductCategory:
      return state.map((productCategory) => {
        if (productCategory.id === action.payload.id) {
          return {
            ...productCategory,
            ...action.payload,
          }
        } else {
          return productCategory
        }
      })
    default:
      return state
  }
}
